import React, { useEffect, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { withMask } from "use-mask-input";
import profile_avatar from "../assets/icons/profile_avatar.png";
import axios from "axios";
import incall_icon from "../assets/icons/incall.svg";
import outcall_icon from "../assets/icons/outcall.svg";
import missedcall_icon from "../assets/icons/missedcall.svg";

const TableItem = ({
  inOut,
  recivedTime,
  personAvatar,
  phone,
  source,
  duration,
}) => {
  return (
    <tr className="table_item"> 
      <td >
        <img
          src={
            inOut == 1
              ? incall_icon
              : inOut == 0
              ? outcall_icon
              : missedcall_icon
          }
        />
      </td>
      <td  title={recivedTime}>
        {new Date(recivedTime).toLocaleTimeString("ru-RU", {
          hour: "2-digit",
          minute: "2-digit",
        })}
      </td>
      <td >
        <img src={personAvatar} />
      </td>
      <td >
        <span>{phone}</span>
      </td>
      <td >
        <span>{source}</span>
      </td>
      <td > оценка</td>
      <td >
        <span>{duration}</span>
      </td>
      <td ></td>
    </tr>
  );
};

const getCallsByTime = (period) => {
  let req = null;
  let past = null;
  let curDate = new Date();
  switch (typeof period) {
    case "number": {
      past = new Date(curDate.getTime() - 86400000 * period);
      req = `date_start=${past.toLocaleDateString("fr-CA", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })}&date_end=${curDate.toLocaleDateString("fr-CA", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })}`;
      break;
    }
    case "object": {
      let start = period.start.getTime();
      let end = period.end.getTime();
      console.log(start, end);
      req = `date_start=${new Date(start).toLocaleDateString("fr-CA", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })}&date_end=${new Date(end).toLocaleDateString("fr-CA", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
      })}`;
    }
  }

  return axios.post(
    `https://api.skilla.ru/mango/getList?${req}`,
    {},
    {
      headers: { Authorization: `Bearer testtoken` },
    }
  );
};

const secToTime = (sec) => {
  let h = sec / 3600;
  let m = (sec / 60).toFixed(0);
  let s = sec % 60;
  // console.log(h,m,s);
  return `${h < 1 ? "" : h + ":"}${m < 1 ? "0:" : m + ":"}${
    s < 10 ? "0" + s : s
  }`;
};

const Calls = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(null);
  const [showCalendar, setShowCalendar] = useState(false);
  const [callsData, setCallsData] = useState([]);
  const [searchCallsByFilters, setSearchCallsByFilters] = useState({
    status: false,
    filteredData: callsData,
  });

  const onChange = (dates) => {
    const [start, end] = dates;
    setStartDate(start);
    setEndDate(end);
  };

  useEffect(() => {
    axios
      .post(
        "https://api.skilla.ru/mango/getList?page=2",
        {},
        {
          headers: { Authorization: `Bearer testtoken` },
        }
      )
      .then((res) => {
        console.log(`search >> `, res);
      });
    axios
      .post(
        "https://api.skilla.ru/mango/getList",
        {},
        {
          headers: { Authorization: `Bearer testtoken` },
        }
      )
      .then((res) => {
        console.log(res);
        setCallsData((v) => {
          v = [...res.data.results];
          return v;
        });
      });
  }, []);

  const selectDateHandler = (e) => {
    console.log(typeof e.target.value);
    switch (e.target.value) {
      case "3days": {
        getCallsByTime(3).then((res) => {
          console.log(`3days >>> `, res);
          setCallsData((v) => {
            v = [...res.data.results];
            return v;
          });
        });
        break;
      }
      case "week": {
        getCallsByTime(7).then((res) => {
          setCallsData((v) => {
            console.log(`week >>> `, res);
            v = [...res.data.results];
            return v;
          });
        });
        break;
      }
      case "month": {
        getCallsByTime(30).then((res) => {
          console.log(`month >>> `, res);
          setCallsData((v) => {});
        });
        break;
      }
      case "year": {
        getCallsByTime(365).then((res) => {
          console.log(`year >>> `, res);
          setCallsData((v) => {
            v = [...res.data.results];
            return v;
          });
        });
        break;
      }
      case "period": {
        setShowCalendar(true);
        break;
      }
      default:
        return;
    }
  };

  const setCalendarCallsDataHandler = () => {
    if (startDate && endDate) {
      getCallsByTime({ start: startDate, end: endDate }).then((res) => {
        console.log(`period >>> `, res);
        console.log(`period DATES >>> `, { start: startDate, end: endDate });
        setCallsData((v) => {
          v = [...res.data.results];
          return v;
        });
      });
      setShowCalendar(false);
    }
  };

  const searchPhoneInputHandler = (e) => {

    if (e.target.value) {
      let val = e.target.value.match(/\d+/g).join("");

      setSearchCallsByFilters((v) => {
        v.filteredData = [...callsData];

        v.filteredData = v.filteredData.filter((el) => {
            console.log(el.from_number.startsWith(val));
          return el.from_number.startsWith(val);
        });
       
        return {
          filteredData: v.filteredData,
          status: true,
        };
      });
      console.log(val, searchCallsByFilters);

    } else {
        console.log(`EMPTY`);
      setSearchCallsByFilters((v) => {
        return {
          ...v,
          status: false,
        };
      });
    }
  };

  return (
    <div className="calls__wrapper">
      <div className="calls__upside_options">
        <div className="calls__balance">
          Баланс: <b>272 Р</b> <span className="calls__balance_add"></span>
        </div>
        <div className="calls__days_option">
          <span className="calls__days_leftarrow" />
          <span className="calls__days_calendar" />
          <select
            className="calls__select_calendar"
            onChange={(e) => selectDateHandler(e)}
            name=""
            id=""
          >
            <option value="3days">3 дня</option>
            <option value="week">Неделя</option>
            <option value="month">Месяц</option>
            <option value="year"> Год</option>
            <option value="period">
              {startDate && endDate
                ? `${startDate.toLocaleDateString("fr-CA", {
                    year: "numeric",
                    month: "2-digit",
                    day: "2-digit",
                  })} - ${endDate.toLocaleDateString("fr-CA", {
                    year: "numeric",
                    month: "2-digit",
                    day: "2-digit",
                  })}`
                : "Указать даты"}
            </option>
          </select>
          <span className="calls__days_rightarrow" />
        </div>
      </div>
      {showCalendar && (
        <div className="calls_calendar">
          <DatePicker
            selected={startDate}
            onChange={onChange}
            maxDate={new Date()}
            startDate={startDate}
            endDate={endDate}
            onClickOutside={() => setCalendarCallsDataHandler()}
            selectsRange
            selectsDisabledDaysInRange
            inline
          />
        </div>
      )}
      <div className="calls__downside_options">
        <input
          onChange={(e) => searchPhoneInputHandler(e)}
          type="search"
          ref={withMask("+7(999)999-99-99")}
          className="calls__input_searchfield"
          placeholder="Поиск по звонкам"
        />
        <div className="calls__dropdowns_options">
          <select name="" id="">
            <option value="" selected>
              Все сотрудники
            </option>
            <option value="">Вася</option>
            <option value="">Петя</option>
            <option value="">Дима</option>
          </select>

          <select name="" id="">
            <option value="" selected>
              Все звонки
            </option>
          </select>

          <select name="" id="">
            <option value="" selected>
              Все ошибки
            </option>
          </select>

          <select name="" id="">
            <option value="" selected>
              Все оценки
            </option>
          </select>
        </div>
      </div>
      <table className="calls__table">
        <thead>
          <tr className="table__head">
            <th>Тип</th>
            <th>Время</th>
            <th>Сотрудник</th>
            <th>Звонок</th>
            <th>Источник</th>
            <th>Оценка</th>
            <th>Длительность</th>
          </tr>
        </thead>
        <tbody>
          { searchCallsByFilters.status ?
            searchCallsByFilters.filteredData.map((el) => {
                return (
                  <TableItem
                    inOut={el.in_out}
                    recivedTime={el.date}
                    personAvatar={el.person_avatar}
                    phone={el.from_number}
                    source={el.source}
                    duration={secToTime(el.time)}
                  />
                );
              })
            :
          callsData.map((el) => {
            return (
              <TableItem
                inOut={el.in_out}
                recivedTime={el.date}
                personAvatar={el.person_avatar}
                phone={el.from_number}
                source={el.source}
                duration={secToTime(el.time)}
              />
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default Calls;
