import React, { useState } from "react";
import Select from "react-select";
import profile_avatar from "../assets/icons/profile_avatar.png";
import arrow_down from "../assets/icons/arrow_down.png";

const HeaderCalls = ({ title, status, statusValue }) => {
  return (
    <>
      <div className="header_calls">
        <p className="calls_message">
          {title.start}{" "}
          <span
            className={
              statusValue < status.low
                ? "red"
                : statusValue < status.medium
                ? "yellow"
                : "green"
            }
          >
            {title.end}
          </span>
        </p>
        <div className="progress_bar">
          <div
            className="progress_bar"
            style={{
              background:
                statusValue < status.low
                  ? "#EA1A4F"
                  : statusValue < status.medium
                  ? "#FFB800"
                  : "#00A775",
              width: `${statusValue}%`,
            }}
          ></div>
        </div>
      </div>
    </>
  );
};

const Header = (props) => {
  const todayOptions = { weekday: "long", month: "long", day: "numeric" };
  const today = new Date();

  const [contragents, setContragents] = useState([
    { label: "ИП Сидорова Александра Михайловна" },
    { label: "ИП Иванов Иван Иванович" },
    { label: "ИП Иванов Иван Степанович" },
  ]);

  const [calls, setCalls] = useState((v) => {
    let newCalls = 20;
    let allCalls = 30;
    let percentage = (newCalls / allCalls) * 100;
    return { newCalls, allCalls, percentage };
  });
  const [callsQuality, setCallsQuality] = useState(40);
  const [callsConversion, setCallsConversion] = useState(67);

  const selectOptionsStyle = (s) => {
    return {    ...s,
        width: "fit-content",
        color: "#899CB1",
        background: "#FFF",
    }
  }
  const selectControlStyles = (s) => {
    return {
    ...s,
    width: "fit-content",
    border: "none",
    color: "#899CB1",
    fontSize: "1rem",
    background: "#FFF",
  }
}


  return (
    <>
      <div className="header__date">
        {today.toLocaleDateString("ru-RU", todayOptions)}
      </div>

      <div className="header__analitic_wrapper">
        <HeaderCalls
          title={{
            start: "Новые звонки",
            end: `${calls.newCalls} из ${calls.allCalls}`,
          }}
          status={{ low: 33, medium: 66 }}
          statusValue={calls.percentage}
        />
        <HeaderCalls
          title={{ start: "Качество разговоров", end: `${callsQuality} %` }}
          status={{ low: 33, medium: 66 }}
          statusValue={callsQuality}
        />
        <HeaderCalls
          title={{ start: "Конверсия в заказ", end: `${callsConversion} %` }}
          status={{ low: 70, medium: 85 }}
          statusValue={callsConversion}
        />
      </div>
      <div className="header__profile">
        <div className="header__profile_search"></div>
        <Select
          styles={{
            option: selectOptionsStyle,
            control: selectControlStyles,
            input: (s) => ({ ...s, color: "#899CB1", fontSize: "1rem" }),
            singleValue: (s) => ({ ...s, color: "#899CB1", fontSize: "1rem" }),
          }}
          components={{
            IndicatorSeparator: false,
          }}
          className="basic-single"
          classNamePrefix="select"
          defaultValue={contragents[0]}
          name="color"
          options={contragents}
        />
        <div className="header__profile_avatar">
          <img src={profile_avatar} alt={contragents[0].label} />
          <span>
            <svg
              height="20"
              width="20"
              viewBox="0 0 20 20"
              aria-hidden="true"
              focusable="false"
            >
              <path
                fill="hsl(0, 0%, 80%)"
                d="M4.516 7.548c0.436-0.446 1.043-0.481 1.576 0l3.908 3.747 3.908-3.747c0.533-0.481 1.141-0.446 1.574 0 0.436 0.445 0.408 1.197 0 1.615-0.406 0.418-4.695 4.502-4.695 4.502-0.217 0.223-0.502 0.335-0.787 0.335s-0.57-0.112-0.789-0.335c0 0-4.287-4.084-4.695-4.502s-0.436-1.17 0-1.615z"
              ></path>
            </svg>
          </span>
        </div>
      </div>
    </>
  );
};

export default Header;
